// libs
import React from "react";

// others
import "./styles.scss";

import HeaderMenuList from "../../../../constants/header/HeaderMenuList";

import { Dropdown, Menu } from "antd";
import UserLogin from "../UserLogin";
import FunctionBox from "../FunctionBox";
import SearchBox from "../SearchBox";
import Logo from "../Logo";
import NewVersion from "../NewVersion";

const menu = (
  <Menu>
    <Menu.Item>
      <a target="_blank" rel="noopener noreferrer" href="https://www.antgroup.com">
        1st menu item
      </a>
    </Menu.Item>
    <Menu.Item>
      <a target="_blank" rel="noopener noreferrer" href="https://www.aliyun.com">
        2nd menu item
      </a>
    </Menu.Item>
    <Menu.Item>
      <a target="_blank" rel="noopener noreferrer" href="https://www.luohanacademy.com">
        3rd menu item
      </a>
    </Menu.Item>
  </Menu>
);

const HeaderList = () => (
  <ul className="action-box">
    <Logo></Logo>
    <NewVersion></NewVersion>
    {HeaderMenuList.map(({ id, name }) => {
      if (name === "Khác")
        return (
          <Dropdown key={id} overlay={menu} placement="bottomRight">
            <li className="more"></li>
          </Dropdown>
        );
      return (
        <Dropdown key={id} overlay={menu} placement="bottomLeft">
          <li key={id} style={{ paddingTop: "3px" }}>
            {name}
          </li>
        </Dropdown>
      );
    })}
    <UserLogin></UserLogin>
    <FunctionBox></FunctionBox>
    <SearchBox></SearchBox>
  </ul>
);
export default HeaderList;
